* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Nunito Sans', sans-serif;
  font-family: 'Old Standard TT', serif;
  font-family: 'Raleway', sans-serif;
  font-family: 'Roboto', sans-serif;
  .element::-webkit-scrollbar {
    width: 0;
  }
  overflow:hidden ;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.content {
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  border: 1px;
  width: 695px;
  height: 425px;
  border-radius: 3%;
}

.categories {
  margin-top: 50px;
  display: flex;
}

.cagegories-list {
  margin: 35px 40px 0;

  li {
    list-style: none;
    margin-top: 12px;
    cursor: pointer;
  }
}

.line {
  width: 1px;
  height: 375px;
  background: #D8D8D8;
}

.categories-list-active {
  font-weight: 700;
}

.categories-header {
  h2 {
    margin-bottom: 15px;
    cursor: default;
  }

  input {
    outline: none;
    border-radius: 1px;
    border: 1px;
    background: #E1DEDE;
    height: 25px;
  }

  margin-left: 38px;
}

.send {
  margin-left: 15px;
  cursor: pointer;
  border-radius: 5px;
  &:hover {
    color: #adb5bd;
  }
}

.categories-content {
  margin-top: 20px;
  max-width: 200px;
  max-height: 200px;
  overflow-x: hidden;
  overflow-y: auto;

  img {
    width: 13px;
    height: 13px;
    cursor: pointer;
    margin-left: 5px;
  }

  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: rgb(255, 255, 255);
    background: linear-gradient(90deg, rgba(255, 255, 255, 1) 40%, rgba(103, 94, 94, 0.6895133053221288) 50%, rgba(103, 94, 94, 0.6895133053221288) 60%, rgba(255, 255, 255, 1) 60%);
  }

  &::-webkit-scrollbar-thumb {
    background: black;
  }

  li {
    input {
      width: 15px;
      height: 20px;
      border: 1px solid #adb5bd;
      border-radius: 50%;
      cursor: pointer;
      margin: 4px;
    }

    display: flex;
    align-items: center;
    list-style: none;
    cursor: default;
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, .6);
  overflow: auto;
  z-index: 999;
  transition: .4s all;
  opacity: 0;
  visibility: hidden;
  cursor: pointer;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.modal__active {
  opacity: 1;
  visibility: visible;
}

.modal-window {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

.modal-bg {
  background: rgba(0, 0, 0, .8);
  width: 100%;
  height: 100%;
  display: flex;
}

.modal-body {
  border: 1px solid white;
  border-radius: 16px;
  padding: 24px;
  margin: auto;
  max-height: 90%;
  background-color: white;
}

.modal-header {
  display: flex;
  justify-content: space-between;
}

.modal-close {
  width: 15px;
  height: 15px;
  text-align: center;

  :hover {
    color: white;
  }

  cursor: pointer;
}

.nav__wrapper {
  width: 100%;
  height: 80px;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);

  .elements__wrapper {
    display: flex;
    justify-content: space-between;
    margin: 20px;
    .nav__logo {
      display: flex;
      justify-content: center;
      align-items: center;
      list-style: none;
      img {
        width: 50px;
        height: 50px;
      }

    }
    .elements__content {
      display: flex;
      justify-content: center;
      align-items: center;
      li {
        list-style: none;
        padding: 10px;
        cursor: pointer;
        :hover{
          font-weight: 700;
        }
        a {
          text-decoration: none;
        }
      }
    }
  }
}

.auth__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.auth__content {
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  border: 1px;
  width: 500px;
  height: 400px;
  border-radius: 3%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  .auth__content__input {
    margin: 30px;
    input {
      outline: none;
      border-radius: 5px;
      border: 1px;
      background: #E1DEDE;
      height: 30px;
      width: 300px;
    }
  }
  .btn__auth{
    padding-left: 200px;
    button {
      cursor: pointer;
      border-radius: 3px;
      border: 0;
      width: 80px;
      height: 25px;
      &:hover{
        color: #adb5bd;
      }
    }
  }
}

.welcome__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;

  .welcome__content {
    .welcome__content__wrapper {

      margin-top: 15px;

    }
  }
}